<template>
  <div class="app-container public-table">
    <el-table :data="adminList" border v-loading="loading" height="calc(100% - 120px)">
      <el-table-column label="创建时间" align="center" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.crtime }}</span>
        </template>
      </el-table-column>
      <el-table-column label="登录IP" align="center" width="160">
        <template slot-scope="scope" v-if="scope.row.last_login_ip.length > 4">
          <span>{{ scope.row.last_login_ip }}</span>
        </template>
      </el-table-column>
      <el-table-column label="用户名" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.username }}</span>
        </template>
      </el-table-column>
      <el-table-column label="所属权限组" align="center" width="360">
        <template slot-scope="scope">
          <el-select v-model="scope.row.permit_groupid" placeholder="未分配" :disabled="scope.row.disabled" @change="submitResetPermit(scope.$index)">
            <el-option v-for="item in permitList" :key="item.groupid" :label="item.name" :value="item.groupid">
            </el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="380">
        <template slot-scope="scope">
          <el-button size="mini" @click="resetPermit(scope.$index)">修改权限组</el-button>
          <el-button size="mini" @click="resetPassword(scope.$index)">修改密码</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index)">删除账号</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 新建管理员  -->
    <div class="created-admin">
      <b v-if="createdUsername && createdPassword"><span style="color: #409eff;margin-right:20px;">账号：{{createdUsername}}</span><span style="color: #ED1C24;margin-right:20px;">密码：{{createdPassword}}</span></b>
      <el-tooltip effect="dark" content="复制账号信息" placement="top" v-if="createdUsername && createdPassword">
        <el-button type="info" v-copy="`账号：${createdUsername}、密码：${createdPassword}`" icon="el-icon-share" circle style="margin-right: 30px;"></el-button>
      </el-tooltip>
      
      <el-button type="primary" style="width: 300px;" @click="createdAdmin">生成一个管理员账号</el-button>
    </div>
    <!-- 删除账号提示  -->
    <el-dialog title="提示" :visible.sync="centerDialogVisible" width="30%" center :append-to-body="true">
      <span>是否删除管理员
        <a href="javascript:;" style="color: red;"> {{deleteUserName}} </a>账号？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmDelete">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import { fetchData } from '@/api'
export default {
  name: 'admin',
  data() {
    return {
      loading: false,
      adminList: [],
      permitList: [],
      deleteUserName: '',
      deleteUserIndex: 0,
      centerDialogVisible: false,
      createdUsername: '',
      createdPassword: ''
    }
  },
  methods: {
    // 获取所有权限组
    fetchPermitGroups() {
      fetchData('fetchPermitGroup').then(data => {
        if (data) this.permitList = data
      })
    },
    // 获取管理员列表
    fetcgmn-adminList() {
      this.loading = true
      fetchData('fetcgmn-adminList').then(data => {
        this.loading = false
        if (data && data.length) {
          data.map(v => v.disabled = true)
          this.adminList = data
        }
      })
    },
    // 修改权限组
    resetPermit(index) {
      if (this.adminList[index].disabled) {
        this.adminList[index].disabled = false
      }
    },
    // 提交权限组修改
    submitResetPermit(index) {
      fetchData('setAdminPermit', {
        uid: this.adminList[index].uid,
        groupid: this.adminList[index].permit_groupid
      }).then(data => {
        if (data) {
          ELEMENT.Message.success('操作成功')
          this.adminList[index].disabled = true
        }
      })
    },
    // 删除管理员
    handleDelete(index) {
      this.deleteUserName = this.adminList[index].username
      this.deleteUserIndex = index
      this.centerDialogVisible = true
    },
    // 确认删除管理员
    confirmDelete() {
      if (this.deleteUserIndex === 0) {
        ELEMENT.Message.error('无法删除')
        this.centerDialogVisible = false
        return false
      }
      fetchData('removeAdmin', {
        id: this.adminList[this.deleteUserIndex].uid
      }).then(data => {
        if (data) {
          this.centerDialogVisible = false
          this.adminList.splice(this.deleteUserIndex, 1)
          ELEMENT.Message.success('操作成功')
        }
      })
    },
    // 修改管理员密码
    resetPassword(index) {
      ELEMENT.MessageBox.prompt('请输入新密码(6~12位数字或字母组合)', '修改密码', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        if (value && value.length > 5) {
          fetchData('resetPassword', {
            id: this.adminList[index].uid,
            old_psw: this.adminList[index].password,
            new_psw: value
          }).then(data => {
            if (data) {
              const user = Cookies.get('username')
              if (user === this.adminList[index].username) {
                Cookies.remove('auth')
                window.location.href = `${window.location.origin}/login`
              } else {
                ELEMENT.Message.success('操作成功')
              }
            }
          })
        } else {
          ELEMENT.Message.error('密码不符合要求')
        }
      }).catch(Err => console.log(Err))
    },
    // 创建管理员
    createdAdmin() {
      ELEMENT.MessageBox.prompt('请输入用户名', '创建账号', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        if (value) {
          fetchData('createdAdmin', {
            username: value
          }).then(data => {
            if (data) {
              this.createdUsername = data.username
              this.createdPassword = data.password
            }
          })
        } else {
          ELEMENT.Message.error('账号不符合要求')
        }
      }).catch(Err => console.log(Err))
    }
  },
  created() {
    this.fetcgmn-adminList()
    this.fetchPermitGroups()
  }
}
</script>

<style lang="less" scoped>
.created-admin {
  margin-top: 20px;
  text-align: right;
  line-height: 40px;
  b {
    margin-right: 20px;
  }
}
</style>




